@charset "UTF-8";
/**
 * Default Setting
 */
html {
  height: 100%; }

body {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 100%;
  margin: 0;
  padding: 0;
  font-size: 0.92em;
  color: #3c3c3c;
  background-color: #0093e9;
  background-image: linear-gradient(-80deg, #BCCFEB 0%, #3488C9 100%);
  background-size: cover; }

a {
  color: #79a1d4;
  text-underline-offset: 4px; }
  a:hover {
    text-decoration: none; }

*,
*:before,
*:after {
  box-sizing: border-box; }

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  font-size: 100%; }

input[type=text],
input[type=password],
input[type=email] {
  appearance: none;
  display: block;
  width: 100%;
  height: 40px;
  margin-top: 5px;
  padding: 0 12px;
  font-size: 16px;
  color: #454545;
  letter-spacing: 1px;
  background-color: rgba(255, 255, 255, 0.2);
  outline: 0;
  border: 1px solid rgba(255, 255, 255, 0.4);
  transition: 235ms ease; }
  input[type=text]:focus,
  input[type=password]:focus,
  input[type=email]:focus {
    border-color: #ffeed5; }
  input[type=text]:not([value=""]),
  input[type=password]:not([value=""]),
  input[type=email]:not([value=""]) {
    border: 1px solid #454545; }

input[type=checkbox] {
  appearance: none; }

