/**
 * Default Setting
 */

// Variables Setting
// @import "app/variables";
$font-family-sans-serif : "游ゴシック", YuGothic, "游ゴシック体", "Yu Gothic", "メイリオ", Meiryo, "ＭＳ Ｐゴシック", "MS PGothic", Robot, "Droid Sans", sans-serif;
$font-family-serif      : "Times New Roman", "游明朝", YuMincho, "ヒラギノ明朝 ProN W3", "Hiragino Mincho ProN", "ＭＳ Ｐ明朝", "MS PMincho", serif;
$font-family-monospace  : "Source Code Pro", "Osaka－等幅", Osaka-mono, "ＭＳ ゴシック", "MS Gothic", monospace;


$color-login-main: #454545;
$color-login-sub1: #79a1d4;
$color-login-sub2: #9fbcdf;
$color-login-sub3: #abcae8;
$color-login-sub4: #ffeed5;

$color-login-text: #3c3c3c;

$color-login-link: $color-login-sub1;

html {
  height: 100%;
}

body {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 100%;
  margin: 0;
  padding: 0;
  font-size: 0.92em;
  color: $color-login-text;
  background-color: #0093e9;
  background-image: linear-gradient(-80deg, #BCCFEB 0%, #3488C9 100%);
  // background-image: linear-gradient(160deg, $color-login-sub1 0%, $color-login-sub4 100%);
  background-size: cover;
}

a {
  color: $color-login-link;
  text-underline-offset: 4px;
  &:hover {
    text-decoration: none;
  }
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

h1,h2,h3,h4,h5,h6 {
  margin: 0;
  font-size: 100%;
}

input[type=text],
input[type=password],
input[type=email] {
  appearance: none;
  display: block;
  width: 100%;
  height: 40px;
  margin-top: 5px;
  padding: 0 12px;
  font-size: 16px;
  color: $color-login-main;
  letter-spacing: 1px;
  background-color: rgba(#fff, .2);
  outline: 0;
  border: 1px solid rgba(#fff, .4);
  transition: 235ms ease;

  &:focus {
    border-color: $color-login-sub4;
  }

  &:not([value=""]) {
    border: 1px solid $color-login-main;
  }
}

input[type=checkbox] {
  appearance: none;
}

#login {
  max-width: 400px !important;
  width: 100% !important;
  margin: 60px auto !important;

  h1 {
    margin-bottom: 32px;
    text-align: center;
    a {
      font-size: 20px !important;
      color: #454545 !important;
      letter-spacing: 1px;
      text-decoration: none !important;
    }

    img {
      max-width: 100%;
      height: auto;
    }
  }
  form {
    max-width: 400px;
    min-width: 300px;
    width: 100%;
    padding: 0 0 26px;
    // padding-bottom: 48px;
    background-color: rgba(#fff, 0);
    border: 0 !important;
    // border: 1px solid $color-login-sub2;
    // box-shadow: 0 5px 15px -2px rgba(#000, .1);
    box-shadow: none !important;

    &:after {
      content: "";
      display: block;
      height: 1px;
      clear: both;
    }

    label {
      font-size: 13px;
      color: #fff !important;

      &[for="password_protected_pass"] {
        width: 100%;
      }
    }

    p {
      margin: 0 0 1em;
      &:last-child {
        margin-bottom: 0;
      }
    }

    .user-pass-wrap {
      margin: 0 0 1em;
    }
    img[alt="CAPTCHA"] {
      display: inline-block;
      vertical-align: top;
      margin-bottom: -1em;
    }

    input[type=text],
    input[type=password],
    input[type=email] {
      appearance: none;
      display: block;
      width: 100%;
      height: 40px;
      margin-top: 5px;
      margin-bottom: 0;
      padding: 0 12px;
      font-size: 16px;
      color: $color-login-main;
      letter-spacing: 1px;
      background-color: rgba(#fff, .2);
      outline: 0;
      border: 1px solid rgba(#fff, .4);
      transition: 235ms ease;

      &:focus {
        background-color: rgba(#fff, .5);
        border-color: #fff !important;
        box-shadow: none !important;
      }

      // &:not([value=""]) {
      //   border: 1px solid $color-login-main;
      // }
    }

    input[type=checkbox] {
      appearance: none;
      &:focus,
      &:checked {
        background-color: #fff !important;
        border-color: #fff !important;
        box-shadow: inherit inherit inherit inherit #fff !important;
      }
    }
  }

  .forgetmenot {
    position: relative;
    // float: left;
    margin-top: 1.6em;
    label {
      cursor: pointer;
      position: relative;
      padding-left: 2.4em;
      user-select: none;
      color: #fff;
    }
    input[type=checkbox] {
      position: absolute;
      top: 50%;
      left: 2px;
      display: block;
      width: 16px;
      height: 16px;
      margin: 0;
      margin-right: 8px;
      outline: 0;
      background-color: rgba(#fff, .2);
      border: 1px solid rgba(#fff, .4);
      transform: translateY(-50%);
      transition: 235ms ease;

      &:before {
        content: "";
        position: absolute;
        top: 2px;
        left: 2px;
        display: block;
        width: 10px;
        height: 6px;
        opacity: 0;
        border-left: 2px solid $color-login-sub1;
        border-bottom: 2px solid $color-login-sub1;
        transform: rotate(-45deg);
        transition: opacity 235ms ease;
      }

      &:focus {
        // outline: 1px solid $color-login-sub4;
        box-shadow: 0 0 2px 1px $color-login-sub4;
      }

      &:checked {
        background-color: $color-login-sub4;
        border-color: darken($color-login-sub4, 5%);

        &:before {
          opacity: 1;
        }
      }
    }
  }

  .submit {
    // float: right;
    margin-top: 32px;
    margin-bottom: 0 !important;
  }

  .button {
    padding: 6px 10px;
    line-height: 1;
    font-size: 15px;
    color: #666;
    letter-spacing: 2px;
    text-decoration: none;
    background-color: #eee;
    border: 1px solid #888;
    border-radius: 4px;
    &:hover {
      opacity: 0.8;
    }
  }

  .wp-hide-pw {
    padding: 0;
    background-color: transparent;
    border: 0;
    outline: 0;
  }

  .button-large {
    padding: 8px 12px;
  }

  .button-primary {
    appearance: none;
    cursor: pointer;
    // display: block;
    // max-width: 200px;
    min-width: 200px;
    // width: 100%;
    // margin: 0 auto;
    color: #fff;
    background-color: #0093e9;
    border: 1px solid darken(#0093e9, 5%);
    // border-radius: 50px;
    border-radius: 4px;
  }

  .submit {
    text-align: center;
    input[name="wp-submit"] {
      padding: 12px 15px;
      font-weight: bold;
      border-radius: 50px;
    }
  }

  .privacy-policy-page-link {
    text-align: right;

    .privacy-policy-link {
      font-size: 12px;
      color: #fff;
      text-decoration: none;
      box-shadow: 0 1px 0 0 rgba(#fff, .5);
    }
  }
}

body.login-action-register {
  #login {
    .submit {
      margin-top: 0 !important;
    }
    #reg_passmail {
      color: #fff;
      text-align: center;
    }
  }
}

body.login-action-checkemail {
  .message {
    position: relative;
    top: 0;
    right: 0;
    padding: 30px 20px;
    color: #fff;
    background-color: rgba(#fff, .2);
    border: 0;

    a {
      color: darken(#0093e9, 5%);
    }
  }
}

body.login-action-confirm_admin_email {
  #login {
    max-width: 640px !important;
    .admin-email-confirm-form {
      max-width: 800px;
      width: 100%;
    }
  }
  #login {
    .admin-email-confirm-form {
      padding: 30px 20px;
      background-color: rgba(#fff, .2);
      border-radius: 10px !important;
      box-shadow: 3px 3px 10px 0 rgba(#000, .1) !important;

      a {
        color: darken(#0093e9, 5%);
      }

      #correct-admin-email {
      }

      .admin-email__heading {
        font-size: 20px;
      }

      .admin-email__actions-secondary {
        a {
          display: inline-block;
          padding: 5px 15px;
          text-decoration: none;
          background-color: #f0f0f0;
          border: 1px solid #888;
          border-radius: 3px;
          &:hover {
            opacity: 0.8;
          }
        }
      }
    }
  }
}

#nav {
  // margin: -2.8em 0 3em;
  padding: 0 0;
  font-size: 12px;
  text-align: right;

  a {
    color: #fff;
  }
}

#backtoblog {
  // display: none;

  a {
    font-size: 14px;
    color: #fff;
  }
}

#login_error {
  // position: fixed;
  // top: 15px;
  // left: 50%;
  // max-width: 90%;
  margin-bottom: 2em;
  padding: 10px 14px;
  font-size: 13px;
  background-color: rgba(#fff, .8);
  // box-shadow: 0 3px 8px -1px rgba(#000, .1);
  border: 1px solid rgba(#fff, .4);
  border-left: 5px solid #e53;
  border-radius: 4px;
  box-shadow: 0 3px 5px 0 rgba(#000, .2);
  transform: translateX(-50%);

  strong {
    color: #e53;
  }
}

.wp-pwd {
  position: relative;

  input[name=pwd] {
    padding-right: 36px;
  }

  .button {
    cursor: pointer;
    position: absolute;
    top: 1px;
    right: 1px;
    bottom: 1px;
    display: block;
    width: 36px;
    padding: 0;
    background-color: transparent;
    border: 0;
    outline: 0;
    border-radius: 0;

    &:focus {
      outline: 1px solid rgba($color-login-main ,.4);
    }

    &:hover {
      .dashicons-visibility {
        color: $color-login-main;
      }
    }
    .dashicons-visibility {
      color: rgba($color-login-main, .6);
    }
    .dashicons-hidden {
      color: $color-login-main;
    }
  }
}


.message {

  min-width: 240px;
  padding: 12px 16px;
  font-family: $font-family-sans-serif;
  font-size: 14px;
  color: #155724;
  background-color: #d4edda;
  border: 1px solid #c3e6cb;
  border-radius: 6px;
  box-shadow: 3px 3px 10px 0 rgba(#000, .1);

  @media (min-width: 641px) {
    // position: fixed;
    // top: 20px;
    // right: 16px;
    z-index: 10;
    margin: 0 0 20px;
  }
}

.admin-email {
  &__actions-primary {
    display: flex;
    justify-content: space-between;
  }
  &__actions-secondary {
    margin: 2em 0 0;
    text-align: right;
  }
}

.language-switcher {
  display: flex;
  justify-content: center;

  form {
    display: flex;
    align-items: center;
  }
  label {
    padding: 5px 6px;
    color: #fff;
  }
  select {
    cursor: pointer;
    appearance: none;
    height: 40px;
    margin: 0 5px;
    padding: 3px 10px;
    padding-right: 40px;
    line-height: 1;
    font-size: 16px;
    color: #454545;
    background-color: rgba(#fff, .2);
    background-image: url('data:image/svg+xml;charset=utf8,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20viewBox%3D%220%200%20122.88%2066.91%22%20xml%3Aspace%3D%22preserve%22%3E%3Cpath%20d%3D%22M11.68%2C1.95C8.95-0.7%2C4.6-0.64%2C1.95%2C2.08c-2.65%2C2.72-2.59%2C7.08%2C0.13%2C9.73l54.79%2C53.13l4.8-4.93l-4.8%2C4.95%20c2.74%2C2.65%2C7.1%2C2.58%2C9.75-0.15c0.08-0.08%2C0.15-0.16%2C0.22-0.24l53.95-52.76c2.73-2.65%2C2.79-7.01%2C0.14-9.73%20c-2.65-2.72-7.01-2.79-9.73-0.13L61.65%2C50.41L11.68%2C1.95L11.68%2C1.95z%22%20fill%3D%22%23424242%22%2F%3E%3C%2Fsvg%3E');
    background-repeat: no-repeat;
    background-position: right 10px center;
    background-size: 12px 8px;
    border: 1px solid rgba(#fff, .4);
    border-radius: 3px;
    box-shadow: 0 2px 5px 0 rgba(#000, .2);
    transition: 235ms ease;

    &:hover {
      background-color: rgba(#fff, .4);
    }
  }
  input[type=submit] {
    cursor: pointer;
    appearance: none;
    margin-left: 8px;
    padding: 5px 12px;
    font-weight: bold;
    color: #666;
    background-color: rgba(#f2f2f2, .8);
    border: 1px solid #c2c2c2;
    border-radius: 4px;
    transition: 235ms ease;

    &:hover {
      background-color: #fff;
    }
  }
}